.projects {

	.works-list {
		display: block;
		list-style-type: none;
		margin: 0;
		padding: 0;
		@include clearfix;

		li {
			display: block;
			padding: 0;
			padding-right: 0;
			padding-top: 0;
			padding-left: 0;
			position: relative;
			margin-bottom: 0.625rem;

			a {
				display: block;
				background-color: $primary;
				width: 100%;
				cursor: pointer;
				background-position: center;
				background-size: cover;
				background-repeat: no-repeat;
				position: relative;
				border: solid 1px $border;
				text-align: center;
				padding-top: rem-calc(20);
				padding-bottom: rem-calc(10);
				padding-left: rem-calc(10);
				padding-right: rem-calc(10);
				z-index: 4999;
				overflow: hidden;

				.themed & {
					background-color: $alt-primary;
					border: solid 1px $alt-border;
				}

				.backdrop {
					position: absolute;
					width: 100%;
					height: 100%;
					top: 0px;
					left: 0;
					z-index: 5;
				}

				h3 {
					position: relative;
					z-index: 6;
					display: inline-block;
					margin: 0;
					padding: 0;
					text-transform: uppercase;
					font-size: rem-calc(30);
					line-height: 1;
					text-align: center;
					@include clearfix;
					color: $secondary;

					.themed & {
						color: $alt-secondary;
					}

				}

				.althold {
					position: absolute;
					left: 0;
					z-index: 5001;
					width: 100%;
					top: 0;
					height: 0px;
					-webkit-transition: all 400ms ease-out;
					-moz-transition: all 400ms ease-out;
					-ms-transition: all 400ms ease-out;
					-o-transition: all 400ms ease-out;
					transition: all 400ms ease-out;
					overflow: hidden;
					display: block;
					background-color: $primary;
					text-align: center;

					.themed & {
						background-color: $alt-primary;
					}
				}

				h3.alt {
					color: $secondary;

					.themed & {
						color: $alt-secondary;
					}
				}

				.project-name {
					display: block;
					padding-right: 0;
					padding-bottom: rem-calc(10);
				}

				.brand {
					display: block;
					border-top: solid 1px $secondary;	
					margin: auto;

					.themed & {
						border-top: solid 1px $alt-secondary;	
					}
				}

				.logo {
					display: block;
					width: rem-calc(135);
					height: rem-calc(60);
					background-image: url("../img/logos.png");
					background-size: 270px 960px;
					background-position: -135px 0px;
					margin: auto;
				}

				h3.alt {
					.brand {
						border-color: $secondary;

						.themed & {
							border-color: $alt-secondary;
						}
					}
				}
			}

			&.glo {
				a {
					h3 {
						.logo {
							background-position: -270px -360px;

							.themed & {
								background-position: -135px -360px;
							}
						}
					}
				}
			}

			&.dan {
