.project-wrapper {
	background-color: $primary;
	background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAFCAYAAACNbyblAAAAJElEQVQYV2NkwAT1jGhi9QwMDI3IgmABkCKYIFwAJogiABIEAM2/BIWEpQYtAAAAAElFTkSuQmCC");
	width: 100%;
	min-height: 100%;
	padding: rem-calc(5);
	position: absolute;
	top: 0;
	left: 0;
	z-index: 9999;
	display: none;

	.themed & {
		background-color: $alt-primary;
	}

	&.opened {
		display: block;
	}

	.projects-inner {
		padding: rem-calc(10);
		height: 100%;
		overflow: hidden;
	}

	.project-details {
		margin: auto;
		background-color: $primary;

		.themed & {
			background-color: $alt-primary;
		}	
	}

	.project {
		min-height: 100%;
		
		@include clearfix;

		&:not(:first-child) {
			display: none;
		}

		.project-images {
			width: 100%;
			float: left;
			height: 100%;
			overflow: hidden;
			margin-top: rem-calc(10);
			@include clearfix;

			.main-image {

				img {
					display: block;
					width: 100%;
					border: solid 1px $aux;
					margin-bottom: rem-calc(10);

					.themed & {
						border-color: $alt-aux;
					}
				}

				span {
					display: none;
				}

			}

			.image-list {
				display: none;
				background-color: $aux;
				margin: 0;
				padding: 0;

				.themed & {
					background-color: $alt-aux;
				}
			}

		}

		.project-info {
			width: 100%;
			float: left;
			height: 100%;

			color: $primary;
			background-color: $aux;
			border: solid 1px $border;

			.themed & {
				color: $alt-secondary;
				background-color: $alt-aux;
				border: solid 1px $alt-aux;
			}

			.awards {
				display: block;
				margin: 0;
				padding: 0;
				list-style-type: none;
				margin-top: rem-calc(20);

				li {
					line-height: 1.5;
					font-size: rem-calc(12);
				}

				li:before {
					display: inline-block;
					margin-right: rem-calc(10);
				}
			}

			.stamps {
				display: block;
				list-style-type: none;
				margin: 0;
				padding: 0;
				@include clearfix;
				margin-top: rem-calc(10);

				li {
					display: block;
					border: solid 1px $border;
					width: rem-calc(44);
					height: rem-calc(44);
					float: left;
					margin-right: rem-calc(5);
					font-size: rem-calc(9);
					text-transform: uppercase;
					text-align: center;
					line-height: 1;
					margin-top: rem-calc(5);

					& > span {
						display: table;
						width: 100%;
						height: 100%;
						
						span {
							display: table-cell;
							vertical-align: middle;
							line-height: 1;
						}
					}

					&.fullstack {
						background: $secondary;
						color: $primary;

						.themed & {
							background: $alt-secondary;
							color: $alt-primary;
							border: solid 1px $alt-secondary;
						}
					}

					&.frontend, &.backend {

						background-color: $secondary;
						height: rem-calc(44);
						font-size: rem-calc(8);

						& > span {
							border-bottom: solid rem-calc(21) $primary;	
							color: $primary;
							height: rem-calc(42);
							overflow: hidden;
						}

						.themed & > span {
							border-color: $alt-primary;
							color: $alt-primary;
						}

						.themed & {
							background-color: $alt-secondary;
							border: solid 1px $alt-secondary;
						}
					
					}

					&.backend {

						& > span {
							border-bottom: 0;
							border-top: solid rem-calc(21) $primary;	
							color: $primary;
							height: rem-calc(44);
						}
					
					}


					.themed & {
						border: solid 1px $alt-secondary;
					}
				}
			}

			.project-plate {
				background-color: $primary;
				color: $secondary;
				padding: rem-calc(10);
				display: none;
				position: relative;

				.details-close {
					display: block;
					position: absolute;
					right: 0;
					top: 0;
					width: rem-calc(44);
					height: rem-calc(44);
					background-color: $secondary;
					color: $primary;
					text-align: center;
					font-size: rem-calc(30);
					line-height: rem-calc(44);
					cursor: pointer;

					&:before {
						width: 100%;
					}

					.themed & {
						background-color: $alt-secondary;
						color: $alt-primary;
					}
				}

				.themed & {
					color: $alt-secondary;
					background-color: $alt-primary;
				}

				h2, h3 {
					margin: 0;
					@include oswald;
					text-transform: uppercase;
					font-size: rem-calc(24);
					line-height: 1;
					padding-right: rem-calc(45);
				}

				h3 {
					font-weight: 300;
					font-size: rem-calc(14);
					line-height: 2;
				}
			}

			.project-description {
				padding: rem-calc(10);

				p {
					font-size: rem-calc(12);
					margin: 0;
					line-height: 1.4;

					a {
						color: $primary;
						display: block;
						margin-top: rem-calc(10);

						.themed & {
							color: $alt-secondary;
						}
					}
				}
			}

			&.top-info {
				margin-top: rem-calc(0);

				.project-plate {
					display: block;
				}
				.project-description {
					display: none;
				}
			}
		}


	}
	
}




@include breakpoint(medium) {

	.projects {
	
		.works-list {

			li {
				padding-left: 0.9375rem;
				margin-bottom: 0.9375rem;
				height: rem-calc(235);

				a {
					height: rem-calc(235);
					padding-top: 0;
					padding-bottom: 0;
					
					h3 {
						font-size: rem-calc(36);
					}

					.logo {
						width: rem-calc(135);
						height: rem-calc(60);
					}
				}

			}

			li:nth-of-type(odd) {
				padding-left: 0;
			}


		}
	}

	.project-wrapper {

		.project-details {
			max-width: rem-calc(640);
		}

		.project {

			.project-images {
				width: 100%;
				height: 100%;
			}

			.project-info {
				width: 100%;

				.awards {

					li {
						font-size: rem-calc(14);
					}

				}

				.project-description {
					padding: rem-calc(15);

					p {
						font-size: rem-calc(14);
					}
				}

				.project-plate {
					padding: rem-calc(15);

					h2 {
						font-size: rem-calc(30);
					}
				}
			}


		}
		
	}

}

