		.themed & {
			border-bottom: solid rem-calc(200) $alt-aux;
		}
	
		.works-list {

			li {

				a {
					min-height: rem-calc(200);
					padding-left: rem-calc(20);
					padding-right: rem-calc(20);
					
					
					h3 {
						font-size: rem-calc(32);
					}

					.project-name {
						margin-top: rem-calc(14);
						padding-right: rem-calc(20);
						float: left;
						text-align: right;
						padding-bottom: 0;
					}

					.brand {
						padding-top: 0;
						padding-left: rem-calc(10);
						float: left;
						border-top: 0;
						border-left: solid 1px $primary;

						.themed & {
							border-left: solid 1px $alt-secondary;
							border-top: 0;
						}
					}

					.logo {
						width: rem-calc(135);
						height: rem-calc(60);
						margin: 0;
					}
				}

				&.jam {
					a {
						.brand {
							padding-left: 0;
						}
					}
				}

				

			}

		}
	}

	.project-wrapper {

		.project-details {
			position: relative;
			max-width: rem-calc(960);
		}
		
		.project {

			height: rem-calc(500);

			.project-images {
				width: rem-calc(640);
				float: left;
				margin-top: 0;

				.main-image {
					width: rem-calc(640);
					height: rem-calc(400);
					position: relative;
					border: solid 1px $aux;
					border-right: 0;
					overflow: hidden;



					.themed & {
						border: solid 1px $alt-aux;
						border-right: 0;
					}

					& > span {
						display: table;
						position: absolute;
						z-index: 3005;
						background-color: $aux;
						width: 100%;
						height: 100%;
						opacity: 1;
						-webkit-transition: all 0ms ease-out;
						-moz-transition: all 0ms ease-out;
						-ms-transition: all 0ms ease-out;
						-o-transition: all 0ms ease-out;
						transition: all 0ms ease-out;
						color: $primary;
						text-align: center;
						font-size: rem-calc(30);

						span {
							display: table-cell;
							vertical-align: middle;
						}

						.themed & {
							background-color: $alt-aux;
							color: $alt-secondary;
						}

						&.loaded {
							-webkit-transition: all 400ms ease-out;
							-moz-transition: all 400ms ease-out;
							-ms-transition: all 400ms ease-out;
							-o-transition: all 400ms ease-out;
							transition: all 400ms ease-out;
							opacity: 0;
						}
					}

					img {
						height: 100%;
						position: absolute;
						margin: 0;
						top: 0;
						left: 0;
						border: 0;
						width: 640px;
						height: 400px;
						z-index: 3000;

						.themed & {
							border: 0;
						}

					}

					img:nth-of-type(1) {
						z-index: 3001;
					}
				}

				.image-list {
					display: block;
					background-color: $secondary;
					margin: 0;
					padding: 0;
					height: rem-calc(100);
					text-align: center;

					.themed & {
						background-color: $alt-aux;
					}

					li {
						display: inline-block;
						margin-left: rem-calc(5);
						margin-right: rem-calc(5);
						margin-top: rem-calc(30);
						border: solid 1px $aux;

						-webkit-transition: all 0ms ease-out;
						-moz-transition: all 0ms ease-out;
						-ms-transition: all 0ms ease-out;
						-o-transition: all 0ms ease-out;
						transition: all 0ms ease-out;

						opacity: 0;

						.themed & {
							border: solid 1px $alt-aux;
						}

						a {

							display: block;
							width: rem-calc(60);
							height: rem-calc(35);
							overflow: hidden;
							border: solid 1px $secondary;
							cursor: pointer;

							.themed & {
								border: solid 1px $alt-aux;
							}
							
						}

						img {
							display: block;
							height: 100%;
							min-width: 100%;
							opacity: 0.6;
						}

						&.active {
							border: solid 1px $primary;

							img {
								opacity: 1;
							}
						}

						.themed &.active {
							border: solid 1px $alt-secondary;
						}

					}

					&.loaded {
						li {

							-webkit-transition: all 400ms ease-out;
							-moz-transition: all 400ms ease-out;
							-ms-transition: all 400ms ease-out;
							-o-transition: all 400ms ease-out;
							transition: all 400ms ease-out;
							opacity: 1;
						
						
						}
					}
				}
			}

			.project-info {
				width: rem-calc(300);
				float: left;
				height: 100%;
				margin-top: 0;
				background-color: $secondary;

				.project-plate {
					display: block;

					h2, h3 {
						margin: 0;
						padding-right: rem-calc(35);
					}

					h2 {
						font-size: rem-calc(30);
					}
				}

			}


		}
		
	}


}


@include breakpoint(xxlarge) {

	.projects {
	
		.works-list {

			li {
				padding-left: 0.9375rem;

				a {
					min-height: rem-calc(200);
					padding: 20px;
					
					h3 {
						font-size: rem-calc(48);
					}

					.project-name {
						margin-top: rem-calc(6);
						padding-right: rem-calc(20);
						float: left;
						text-align: right;
					}

					.brand {
						padding-top: 0;
						padding-left: rem-calc(10);
						float: left;
						border-top: 0;
					}

					.logo {
						width: rem-calc(135);
						height: rem-calc(60);
					}
				}

			}

		}
	}



}
